<template>
<div class="container">
  <p class="deadline">{{article.deadline}}</p>
  <div v-for="(chapter,index) in article.content" :class="[index === 0 ? firstChapter : otherChapter]">
    <p class="chapter-title">{{chapter.title}}</p>
    <div v-for="(item, index) in chapter.item" :class="[index === 0 ? firstItem : otherItem]">
      <p v-if="item.title" class="chapter-item-title">{{item.title}}</p>
      <p class="chapter-item-text" v-for='textItem in item.text'>{{textItem}}</p>
      <img class="img" v-if="item.img" :src="item.img">
    </div>
  </div>
</div>
</template>

<script>

export default {
  name: 'el-article',

  props: {
    article: {
      type: Object,
      default: {
        deadline: '',
        content: [
          {
            title: '?????????',
            item: [
              {
                title: '?????????',
                text: [
                  '?????????',
                  '?????????'
                ]
              }
            ]
          }
        ]
      }
    }
  },
  data() {
    return {
      firstChapter: 'first-chapter',
      otherChapter: 'other-chapter',
      firstItem: 'first-item',
      otherItem: 'other-item'
    }
  }
}
</script>

<style lang="postcss">
  .container {
    width: 8.293333333333333rem;
    margin: 0 auto;
    font-weight: 600;
    line-height: 0.72rem;
    letter-spacing: 1px;
    color: #222;
    .title {
      font-size: 17px;
      letter-spacing: 1.06px;
      text-align: center;
    }
    .deadline {
      font-weight: 500;
      font-size: 13px;
      color: #4A4A4A;
      letter-spacing: 1px;
      margin-top: 0.6933333333333334rem;
    }
    .first-chapter {
      margin-top: 0.26666666666666666rem;
    }
    .other-chapter {
      margin-top: 0.6933333333333334rem;
    }
    .first-item {
      margin-top: 0.13333333333333333rem;
    }
    .other-item {
      margin-top: 1.0933333333333333rem;
    }
    .chapter-title {
      font-size: 16px;
    }
    .chapter-item-title {
      font-size: 14px;
    }
    .chapter-item-text {
      font-weight: 500;
      font-size: 13px;
      color: #4A4A4A;
      margin-top: 0.26666666666666666rem;
    }
    .img {
      width: 100%;
    }
  }
</style>



